import {MatPaginatorIntl} from '@angular/material';

export class  PaginatorEspa√±ol extends MatPaginatorIntl { 
    itemsPerPageLabel = 'Items por P√°gina'; 
    nextPageLabel  = 'Siguiente'; 
    previousPageLabel = 'Previa'; 
    firstPageLabel = "Primera P√°gina";
    lastPageLabel = "√öltima P√°gina"

    getRangeLabel = function (page, pageSize, length) { 
    if (length === 0 || pageSize === 0) { 
     return '0 de ' + length; 
    } 
    length = Math.max(length, 0); 
    const startIndex = page * pageSize; 
    //Si el √≠ndice de inicio excede la longitud de la lista, no intente arreglar el √≠ndice final hasta el final
    const endIndex = startIndex < length ? Math.min(startIndex + pageSize, length) :  startIndex + pageSize; 
    return startIndex + 1 + ' - ' + endIndex + ' de ' + length; 
    }; 
} 